export const parseError = (error) => {
    if(typeof error === 'string') return error;
    if(error?.data?.errors || error?.data?.message) return swaggerParseError(error);
    if(error?.error || error?.status) return serverError(error);
}

const swaggerParseError = (data) => {
    if(data?.data?.message) {
        if(data.data.message === 'Internal server error') return '  ';
    }

    const errors = Object.values(data.data.errors);
    const errorsStr = errors.reduce((sumString, curString) => {
        if(curString === 'incorrectPassword') return sumString + '    . ';
        if(curString === 'emailNotExists') return sumString + '   . ';
        if(curString === 'firstName should not be empty') return sumString + '  . ';
        if(curString === 'lastName should not be empty') return sumString + '  . ';
        if(curString === 'emailAlreadyExists') return sumString + '   . ';
    }, '');

    return errorsStr;
}

const serverError = (error) => {
    if(error.error === "TypeError: Failed to fetch") return "     ";
    if(error.status === 404) return "404.   "
}